import React from 'react'
//import clsx from 'clsx'

import i18n from '../../../controller/i18n'

import Dialog from '../../../components/Dialog'
import Inputs from '../../../components/Inputs'

import { JsonEditor as Editor } from 'jsoneditor-react'
import 'jsoneditor-react/es/editor.min.css'


/*
 *
 */
const JsonEditor = React.forwardRef((props, ref) => {
	const { val, height, _onChange } = props;
	
	return (
		<Editor
			ref={ref}
			value={val || {}}
			mode='text'
			allowedModes={['tree', 'view', 'form', 'text']}
			htmlElementProps={{ style: { 'height': height || 0, 'display': !height ? 'none' : 'block' } }}
			onChange={_onChange}
			/>
	);
});

/*
 *
 */
export const JsonEditorPopup = React.memo(props => {
	const { open, _onClose } = props;
	
	return (
		<Dialog
			header={i18n.t('JSON Editor')}
			actions={
				<Inputs.Button key="action-save" variant="text" onClick={_onClose} color="primary" label="Apply" />
			}
			
			dense
			maxWidth='sm'
			fullWidth={true}
			open={open}
			>
			
			<JsonEditor {...props} />
		</Dialog>
	);
});

export default JsonEditor;
